import Swiper, { Navigation, Thumbs } from "swiper";
Swiper.use([Navigation]);
Swiper.use([Thumbs]);
const swiper = new Swiper(".swiper-mexanizm-section", {
  slidesPerView: "auto",
  spaceBetween: 20,
  navigation: {
    nextEl: ".swiper-next",
    prevEl: ".swiper-prev",
  },
  breakpoints: {
    767: {
      spaceBetween: 30,
      slidesPerView: "auto",
    },
    700: {
      slidesPerView: 3,
    },
  },
});
const swiperCardThumbs = new Swiper(".swiper-card-thumbs", {
  spaceBetween: 5,
  slidesPerView: 7,
  freeMode: true,
  watchSlidesProgress: true,
  breakpoints: {
    768: {
      spaceBetween: 10,
      slidesPerView: 6,
    },
  },
});
const swiperCard = new Swiper(".swiper-card", {
  spaceBetween: 10,
  navigation: {
    nextEl: ".swiper-next",
    prevEl: ".swiper-prev",
  },
  thumbs: {
    swiper: swiperCardThumbs,
  },
});
